.homepage-header {
    margin: 30px 20px;
}

.homepage-header p {
    font-family: "Poppins", sans-serif;
    font-size: 19px;
    color: grey;
    font-weight: 600;
    margin: 5px 0;
}

.homepage-header div {
    background-color: var(--edificeGreen);
    height: 7px;
    width: 100px;
    border-radius: 20px;
}

.homepage-course-cards-list {
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: 5rem;
}

.loading-spin-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5%;
}
@media (max-width: 1000px) {
    .homepage-course-cards-list {
        justify-content: center;
    }
}
