.course-card {
    cursor: pointer;
    background-color: var(--lightGreen);
    border-radius: 10px;
    margin: 10px;
    padding: 15px;
    max-width: 200px;
    min-width: 200px;
    transition: 0.2s;
}

.course-card:hover {
    transform: scale(1.04);
    box-shadow: 0px 1px 10px rgb(238, 238, 238);
}

.course-card:active {
    transform: scale(0.98);
    box-shadow: none;
    background: var(--midGreen);
}

.course-card div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.course-card p:nth-child(2) {
    font-weight: 500;
    max-width: 200px;
    text-overflow: ellipsis;
    font-weight: 600;
}

.course-card p:nth-child(3) {
    font-weight: 500;
    font-size: 0.8rem;
    font-weight: bold;
    max-width: 200px;
    text-overflow: ellipsis;
}

.thumbnail {
    overflow: hidden;
    border-radius: 10px;
    width: 110px;
    height: 150px;
    background-color: var(--edificeGreen);
}

.thumbnail img {
    width: 100%;
    background-size: cover;
}
