.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
}

.navbar-left-portion {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: 0.2s;
}

.navbar-left-portion:hover{
    transform: scale(1.02);
}

.navbar-left-portion:active{
    transform: scale(0.99);
}

.navbar-left-portion img {
    height: 50px;
    padding-right: 0.5rem;
}

.navbar-left-portion p {
    color: var(--edificeGreen);
    font-size: 1.7rem;
}

.navbar-right-portion p {
    background: var(--edificeGreen);
    background-color: var(--edificeGreen);
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    transition: 0.2s;
}

.navbar-right-portion p:hover{
    transform: scale(1.04);
    background-color: rgb(241, 75, 75);
}
