.course-page-container {
    display: flex;
    height: 100vh;
}

.course-page-video-container {
    display: flex;
    flex-direction: column;
    width: 70%;
    padding: 0 1rem;
}

.ytTitle {
    font-size: 1.4rem;
    font-weight: bold;
}

.ytBody {
    margin-top: -10px;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(100, 100, 100);
}

.yt-player {
    width: 100%;
    height: 70vh;
}

.course-name-lessons-container {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    width: 30%;
    overflow: scroll;
}

.course-name-lessons-container .active-lesson-card {
    border: 2px solid var(--edificeGreen);
}

.course-name-lessons-container div {
    border: 2px solid transparent;
    cursor: pointer;
    transition: 0.2s;
    background-color: white;
    box-shadow: 1px 2px 10px rgb(226, 233, 224);
    margin-bottom: 1rem;
    padding: 1rem;
    border-radius: 1rem;
}

.course-name-lessons-container div p {
    font-size: 1.2rem;
    font-weight: bold;
}

.course-name-lessons-container div p:nth-child(2) {
    font-size: 1rem;
    font-weight: bold;
    color: rgb(100, 100, 100);
}

.course-name-lessons-container div:active {
    transform: scale(0.95);
}


@media (max-width: 1000px){
    .course-page-container{
       flex-direction: column; 
    }
    .course-name-lessons-container{
        width: 100%;
        overflow: visible;
    }
    .course-page-video-container{
        width: 100%;
    }
    .yt-player{
        height: 30vh;
    }
    .ytBody{
        margin-bottom: 2rem;
    }
} 