@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
  font-family: "Poppins", "Montserrat", "Gilroy", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 4px;
  height: 10px;
}

::-webkit-scrollbar-track {
  border-radius: 0;
  background: var(--courseCardBackground);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: var(--midGreen);
}

::-webkit-scrollbar-thumb:active {
  border-radius: 0;
  background: var(--edificeGreen);
}
:root {
    --edificeGreen: #00bc4e;
    --lightGreen: #f6fdf8;
    --midGreen: #00bc4e33;
    --courseCardImage: rgb(155, 232, 188);
    --white: #ffffff;
    --errorRed: #cc2828;
}

.homepage-header {
    margin: 30px 20px;
}

.homepage-header p {
    font-family: "Poppins", sans-serif;
    font-size: 19px;
    color: grey;
    font-weight: 600;
    margin: 5px 0;
}

.homepage-header div {
    background-color: var(--edificeGreen);
    height: 7px;
    width: 100px;
    border-radius: 20px;
}

.homepage-course-cards-list {
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: 5rem;
}

.loading-spin-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5%;
}
@media (max-width: 1000px) {
    .homepage-course-cards-list {
        justify-content: center;
    }
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
}

.navbar-left-portion {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: 0.2s;
}

.navbar-left-portion:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.navbar-left-portion:active{
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}

.navbar-left-portion img {
    height: 50px;
    padding-right: 0.5rem;
}

.navbar-left-portion p {
    color: var(--edificeGreen);
    font-size: 1.7rem;
}

.navbar-right-portion p {
    background: var(--edificeGreen);
    background-color: var(--edificeGreen);
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    transition: 0.2s;
}

.navbar-right-portion p:hover{
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    background-color: rgb(241, 75, 75);
}

.course-card {
    cursor: pointer;
    background-color: var(--lightGreen);
    border-radius: 10px;
    margin: 10px;
    padding: 15px;
    max-width: 200px;
    min-width: 200px;
    transition: 0.2s;
}

.course-card:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    box-shadow: 0px 1px 10px rgb(238, 238, 238);
}

.course-card:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
    box-shadow: none;
    background: var(--midGreen);
}

.course-card div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.course-card p:nth-child(2) {
    font-weight: 500;
    max-width: 200px;
    text-overflow: ellipsis;
    font-weight: 600;
}

.course-card p:nth-child(3) {
    font-weight: 500;
    font-size: 0.8rem;
    font-weight: bold;
    max-width: 200px;
    text-overflow: ellipsis;
}

.thumbnail {
    overflow: hidden;
    border-radius: 10px;
    width: 110px;
    height: 150px;
    background-color: var(--edificeGreen);
}

.thumbnail img {
    width: 100%;
    background-size: cover;
}

.login-wrapper {
    background-color: var(--edificeGreen);
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.login-container {
    background-color: white;
    padding: 20px;
    width: 450px;
    border-radius: 15px;
    box-shadow: 0 0 20px 5px rgba(128, 128, 128, 0.5);
}

.login-header-row {
    display: flex;
    width: 50vh;
}

.login-header-row img {
    height: 45px;
    padding-right: 15px;
}

.login-header-row p {
    color: grey;
    font-weight: bold;
}

.login-header-desc {
    margin: 35px 0px;
    color: grey;
    font-size: 15px;
}

.login-input-container {
    display: flex;
    flex-direction: column;
    height: 120px;
    justify-content: space-between;
}

.login-input-container input {
    outline: none;
    border: 1px solid rgba(0, 188, 78, 0.5);
    border-bottom: 1px solid var(--edificeGreen);
    font-size: 16px;
    padding: 10px;
    border-radius: 10px;
    color: var(--edificeGreen);
}

.login-input-container input::-webkit-input-placeholder {
    color: var(--edificeGreen);
    opacity: 0.5;
}

.login-input-container input:-ms-input-placeholder {
    color: var(--edificeGreen);
    opacity: 0.5;
}

.login-input-container input::placeholder {
    color: var(--edificeGreen);
    opacity: 0.5;
}

.login-forgot-password-container {
    display: flex;
}

.login-forgot-password-container p {
    color: var(--edificeGreen);
    font-size: 13px;
    cursor: pointer;
    font-weight: bold;
}

.login-footer-row {
    padding: 50px 0px 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.login-footer-row .login-text-button {
    display: flex;
    font-size: 13px;
    color: grey;
}

.login-footer-row .login-text-button p:nth-child(2) {
    color: var(--edificeGreen);
    padding-left: 5px;
    cursor: pointer;
    font-weight: bold;
}

.login-footer-row .login-button {
    cursor: pointer;
    display: flex;
    width: 45%;
    justify-content: center;
    align-items: center;
    background-color: var(--edificeGreen);
    border-radius: 10px;
    height: 35px;
    font-size: 15px;
    color: white;
    font-weight: bold;
    transition: 0.2s;
}

.login-footer-row .login-button:hover{
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
}

.login-footer-row .login-button:active{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.course-page-container {
    display: flex;
    height: 100vh;
}

.course-page-video-container {
    display: flex;
    flex-direction: column;
    width: 70%;
    padding: 0 1rem;
}

.ytTitle {
    font-size: 1.4rem;
    font-weight: bold;
}

.ytBody {
    margin-top: -10px;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(100, 100, 100);
}

.yt-player {
    width: 100%;
    height: 70vh;
}

.course-name-lessons-container {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    width: 30%;
    overflow: scroll;
}

.course-name-lessons-container .active-lesson-card {
    border: 2px solid var(--edificeGreen);
}

.course-name-lessons-container div {
    border: 2px solid transparent;
    cursor: pointer;
    transition: 0.2s;
    background-color: white;
    box-shadow: 1px 2px 10px rgb(226, 233, 224);
    margin-bottom: 1rem;
    padding: 1rem;
    border-radius: 1rem;
}

.course-name-lessons-container div p {
    font-size: 1.2rem;
    font-weight: bold;
}

.course-name-lessons-container div p:nth-child(2) {
    font-size: 1rem;
    font-weight: bold;
    color: rgb(100, 100, 100);
}

.course-name-lessons-container div:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}


@media (max-width: 1000px){
    .course-page-container{
       flex-direction: column; 
    }
    .course-name-lessons-container{
        width: 100%;
        overflow: visible;
    }
    .course-page-video-container{
        width: 100%;
    }
    .yt-player{
        height: 30vh;
    }
    .ytBody{
        margin-bottom: 2rem;
    }
} 
