.login-wrapper {
    background-color: var(--edificeGreen);
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.login-container {
    background-color: white;
    padding: 20px;
    width: 450px;
    border-radius: 15px;
    box-shadow: 0 0 20px 5px rgba(128, 128, 128, 0.5);
}

.login-header-row {
    display: flex;
    width: 50vh;
}

.login-header-row img {
    height: 45px;
    padding-right: 15px;
}

.login-header-row p {
    color: grey;
    font-weight: bold;
}

.login-header-desc {
    margin: 35px 0px;
    color: grey;
    font-size: 15px;
}

.login-input-container {
    display: flex;
    flex-direction: column;
    height: 120px;
    justify-content: space-between;
}

.login-input-container input {
    outline: none;
    border: 1px solid rgba(0, 188, 78, 0.5);
    border-bottom: 1px solid var(--edificeGreen);
    font-size: 16px;
    padding: 10px;
    border-radius: 10px;
    color: var(--edificeGreen);
}

.login-input-container input::placeholder {
    color: var(--edificeGreen);
    opacity: 0.5;
}

.login-forgot-password-container {
    display: flex;
}

.login-forgot-password-container p {
    color: var(--edificeGreen);
    font-size: 13px;
    cursor: pointer;
    font-weight: bold;
}

.login-footer-row {
    padding: 50px 0px 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.login-footer-row .login-text-button {
    display: flex;
    font-size: 13px;
    color: grey;
}

.login-footer-row .login-text-button p:nth-child(2) {
    color: var(--edificeGreen);
    padding-left: 5px;
    cursor: pointer;
    font-weight: bold;
}

.login-footer-row .login-button {
    cursor: pointer;
    display: flex;
    width: 45%;
    justify-content: center;
    align-items: center;
    background-color: var(--edificeGreen);
    border-radius: 10px;
    height: 35px;
    font-size: 15px;
    color: white;
    font-weight: bold;
    transition: 0.2s;
}

.login-footer-row .login-button:hover{
    transform: scale(1.04);
}

.login-footer-row .login-button:active{
    transform: scale(1);
}
